@charset "UTF-8";
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
/* Couleurs */
.clearfix:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}
.container {
  width: 64rem;
  margin: 0 auto;
}
@font-face {
  font-family: 'decour-black';
  src: url('../fonts/Decour-Black.otf');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'diaria-light';
  src: url('../fonts/DiariaProLight.otf');
  font-weight: normal;
  font-style: normal;
}
* {
  box-sizing: border-box;
}
html {
  font-size: 125%;
}
body {
  font-size: 1rem;
  line-height: 1.5;
  font-family: 'diaria-black', serif;
  color: #000;
}
h1,
h2,
h3 {
  font-family: 'decour-light', serif;
}
h1 {
  margin-bottom: 0.75rem;
  font-size: 2rem;
  line-height: 1;
}
h2 {
  margin-bottom: 1rem;
  font-size: 1.5rem;
  line-height: 1;
}
h3 {
  margin-bottom: 1.2rem;
  font-size: 1.25rem;
  line-height: 1.2;
}
p,
blockquote,
ul,
ol,
dl,
aside,
figure,
audio,
.video {
  margin-bottom: 1.5rem;
}
blockquote {
  padding: 1.5rem 3rem;
  font-size: 3rem;
  font-style: italic;
  text-align: center;
}
ul,
ol {
  list-style-position: inside;
}
ul {
  list-style-type: disc;
}
ul ul {
  list-style-type: circle;
}
ol {
  list-style-type: decimal;
}
dt {
  font-weight: bold;
}
.publication {
  font-style: italic;
}
.chapo {
  font-weight: bold;
}
.partage {
  list-style: none;
}
a {
  color: orange;
}
a:focus,
a:hover {
  text-decoration: none;
}
strong {
  font-weight: bold;
}
em,
cite {
  font-style: italic;
}
sub,
sup {
  position: relative;
  font-size: 80%;
  font-variant: normal;
  line-height: 0;
  vertical-align: baseline;
}
sub {
  bottom: -0.5em;
}
sup {
  top: -0.5em;
}
img,
audio,
video,
iframe {
  max-width: 100%;
  height: auto;
}
figcaption {
  padding-bottom: 1rem;
  border-bottom: 1px solid #000;
  font-style: italic;
}
.video {
  position: relative;
  padding-bottom: 56.25%;
  /* 16:9 */
  padding-top: 25px;
  height: 0;
  overflow: hidden;
}
.video iframe,
.video object,
.video embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
aside {
  padding: 1rem;
  background: #eee;
}
.center {
  text-align: center;
}
.left {
  float: left;
}
.right {
  float: right;
}
.colonne,
figcaption {
  float: left;
  padding-left: 0.66666667rem;
  padding-right: 0.66666667rem;
  float: none;
  margin: 0 auto;
}
.colonne-deux-tiers {
  float: left;
  padding-left: 0.66666667rem;
  padding-right: 0.66666667rem;
  width: 66.66666667%;
}
.articles-connexes {
  clear: left;
  padding-left: 0;
}
.articles-connexes article {
  float: left;
  padding-left: 0.66666667rem;
  padding-right: 0.66666667rem;
}
@media (min-width: 768px) {
  .colonne,
  figcaption {
    width: 66.66666667%;
  }
  .articles-connexes article {
    width: 33.33333333%;
  }
}
@media (min-width: 992px) {
  .colonne,
  figcaption {
    width: 50%;
  }
}
